@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,700;1,300;1,400&family=Nunito+Sans:wght@200;300;400;700&display=swap');

* {
    /* colors */
    --head-bg-color: rgb(49, 86, 104);
    --head-bg-color-light: rgb(49, 86, 104, 0.3);
    --bg-color: rgb(208, 207, 207);
    --title-color: rgb(77, 77, 77);
    /* fonts */
    --head-font: 'Montserrat Alternates', sans-serif;
    --body-font: 'Nunito Sans', sans-serif;
    /* shadow */
    --shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

body {
    margin: 0;
    background-color: var(--bg-color);
    font-family: var(--body-font);
}

.header {
    background-color: var(--head-bg-color);
    color: var(--bg-color);
    text-align: center;
    padding: 16px;
    font-family: var(--head-font);
    font-size: 24px;
    letter-spacing: 2px;
    box-shadow: var(--shadow);
}

.general_container,
.education_container,
.practical_container {
    display: flex;
    flex-direction: column;
}

.general_title,
.education_title,
.practical_title {
    margin: 16px 20%;
    font-size: 28px;
    color: var(--title-color);
    letter-spacing: 1px;
}

form {
    display: flex;
    flex-direction: column;
}
 
.gformToHide,
.eformToHide,
.pformToHide  {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: auto;
    gap: 12px;
    padding-bottom: 24px;
}

input {
    height: 32px;
    border-radius: 8px;
    border: 1px solid var(--title-color);
    padding-left: 8px;
    letter-spacing: 1px;
}

input:focus {
    outline: 1.5px solid var(--head-bg-color);
    box-shadow: var(--shadow);
    transition: 0.5s;
}

/*form > input:invalid {
    background-color: rgb(199, 173, 173);
}*/

button {
    width: 112px;
    padding: 8px 16px;
    border-radius: 8px;
    border: 1px solid var(--title-color);
    margin-top: 8px;
    letter-spacing: 1px;
    color: var(--title-color);
    align-self: flex-end;
    margin-right: 20%;
}

.previewBtn {
    margin: 5% 10%;
    
}

.gEdit,
.eAdd,
.pAdd {
    display: none;
}

button:hover {
    cursor: pointer;
    background-color: var(--head-bg-color);
    color: var(--bg-color);
    transition: 0.5s;
    box-shadow: var(--shadow);
}

.dates {
    display: flex;
    align-items: center;
    gap: 10%;
    margin: 16px;
}

.dates > div {
    display: flex;
    align-items: center;
    gap: 8px;
}

#eFrom, 
#eUntil, 
#pFrom,
#pUntil {
    padding: 4px;
    border-radius: 8px;
    border: 1px solid var(--title-color);
    font-family: var(--body-font);
}

#eFrom:hover, 
#eUntil:hover,
#pFrom:hover, 
#pUntil:hover {
    cursor: pointer;
}

#eFrom:focus, 
#eUntil:focus, 
#pFrom:focus,
#pUntil:focus {
    outline: 1.5px solid var(--head-bg-color);
    box-shadow: var(--shadow);
    transition: 0.5s;
}

.display_container{
    display: none;
    background-color: white;
    margin: 5% 10%;
    border-radius: 8px;
    padding: 2% 4%;
}

.display_general,
.display_education,
.display_practical {
    padding-bottom: 8px;
    border-bottom: 1px solid var(--head-bg-color-light);
}

.display_section_title {
    font-size: 24px;
    color: var(--head-bg-color);
}

.display_general > p {
    margin: 4px;
}

.school_card,
.job_card {
    background-color: var(--head-bg-color-light);
    padding: 1% 5%;
    margin: 8px;
    border-radius: 16px;
    border: 1px solid var(--head-bg-color);
}

.sc_dates,
.jc_dates {
    font-size: 12px;
}

.sc_school,
.jc_company {
    font-size: 20px;
    margin: 0;
}

.sc_study,
.jc_position {
    font-size: 14px;
    margin: 4px 0;
}

@media only screen and (max-width: 500px) {
    .dates {
        flex-direction: column;
        gap: 8px;

    }

    .dates > div > input {
        height: 20px;
    }

    .gformToHide,
    .eformToHide,
    .pformToHide {
        padding-bottom: 0;
    }
}